html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

a {
  text-decoration: none;
  color: inherit;
  display: inline-block; }

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0; }

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0; }

img {
  max-width: 100%;
  height: auto; }

body {
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  background: #F9FBFD; }

.container {
  max-width: 1190px;
  margin: 0 auto;
  padding: 0 15px; }

.title {
  font-weight: 600;
  font-size: 28px;
  color: #011C40; }

.header {
  min-height: 650px;
  background: blue center center/cover no-repeat;
  position: relative;
  color: #F9FBFD; }
  .header__top {
    padding-top: 30px;
    padding-bottom: 210px;
    display: flex;
    align-items: center;
    justify-content: space-between; }
  .header__middle {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 95px; }
  .header__title {
    font-weight: bold;
    font-size: 54px;
    margin-bottom: 30px; }
  .header__subtitle {
    font-weight: 400;
    font-size: 20px; }

.nav {
  color: #F9FBFD; }
  .nav__list {
    display: flex; }
  .nav__item:not(:last-child) {
    margin-right: 33px; }
  .nav__link {
    font-size: 16px; }

.journey {
  padding: 120px 0; }
  .journey__row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%; }
  .journey__text {
    font-size: 16px;
    line-height: 150%;
    color: #011C40;
    max-width: 530px;
    margin-right: 30px; }
  .journey__title {
    margin-bottom: 40px; }

.advantages {
  background: #E3ECF6 center center/cover no-repeat;
  padding: 120px 0; }
  .advantages__title {
    margin-bottom: 60px;
    text-align: center; }
  .advantages__list {
    display: flex;
    justify-content: space-between; }
  .advantages__item {
    flex-basis: 360px;
    background: #F9FBFD;
    padding: 30px;
    color: #011C40; }
  .advantages__icon {
    margin-bottom: 30px; }
  .advantages__caption {
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 20px; }
  .advantages__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 140%; }

.cruises {
  padding: 120px 0 90px; }
  .cruises__title {
    text-align: center;
    margin-bottom: 60px; }
  .cruises__list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    color: #F9FBFD;
    margin-right: -30px;
    max-width: 1160px; }
  .cruises__item {
    position: relative;
    width: 260px;
    margin-bottom: 30px;
    margin-right: 30px; }
    .cruises__item:hover .cruises__show {
      visibility: visible; }
  .cruises__headline {
    position: absolute;
    bottom: 30px;
    left: 30px;
    font-weight: 500;
    font-size: 18px; }
  .cruises__show {
    visibility: hidden;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    padding: 35px 25px;
    background: #011C40;
    box-shadow: 0px 4px 35px rgba(1, 28, 64, 0.15); }
  .cruises__caption {
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 25px; }
  .cruises__name {
    margin-bottom: 10px;
    font-size: 14px; }
  .cruises__text {
    display: block;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: #D8D8D9; }
  .cruises__btn {
    position: absolute;
    bottom: 30px;
    background: #F9FBFD;
    font-weight: 600;
    font-size: 14px;
    padding: 15px 47px;
    color: #011C40; }

.reservation {
  padding: 50px 0;
  background: #E3ECF6 center center/cover no-repeat; }
  .reservation__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1040px;
    margin: 0 auto; }
  .reservation__text {
    max-width: 500px;
    color: #011C40; }
  .reservation__title {
    font-weight: bold;
    font-size: 40px; }
  .reservation__descr {
    font-size: 18px;
    line-height: 150%;
    font-weight: 400;
    max-width: 350px; }

.form {
  max-width: 404px;
  width: 100%;
  padding: 50px;
  background: #011C40;
  box-shadow: 0px 4px 45px rgba(1, 28, 64, 0.15);
  color: #F9FBFD; }
  .form__title {
    margin-bottom: 35px;
    font-size: 18px;
    line-height: 150%; }
  .form__row:not(:last-child) {
    margin-bottom: 20px; }
  .form__row-checkbox {
    display: flex;
    align-items: center; }
  .form__input {
    background: #F9FBFD;
    border: none;
    width: 100%;
    padding: 16px; }
  .form__input-checkbox {
    visibility: hidden;
    opacity: 0; }
    .form__input-checkbox:checked + .form__checkbox-text::before {
      background: #F9FBFD url(../img/icons/checker.svg) center center no-repeat; }
  .form__checkbox-text {
    font-size: 10px;
    line-height: 130%;
    font-weight: 400;
    position: relative;
    padding-left: 6px; }
    .form__checkbox-text::before {
      content: '';
      position: absolute;
      width: 18px;
      height: 18px;
      background: #F9FBFD;
      left: -16px; }
  .form__btn {
    background: #F9FBFD;
    border: none;
    width: 100%;
    padding: 16px;
    margin-top: 10px;
    cursor: pointer; }

.contacts {
  padding: 120px 0;
  color: #011C40; }
  .contacts__inner {
    display: flex;
    align-items: center;
    justify-content: space-between; }
  .contacts__text {
    max-width: 500px; }
  .contacts__caption {
    font-size: 18px;
    line-height: 150%; }
  .contacts__address {
    display: flex;
    align-items: center; }
    .contacts__address span {
      font-size: 16px;
      font-weight: 400;
      margin-left: 10px; }
  .contacts__phone {
    display: flex;
    align-items: center; }
    .contacts__phone a {
      font-size: 16px;
      font-weight: 400;
      margin-left: 10px; }

.footer {
  background: #011C40; }
  .footer__inner {
    display: flex;
    min-height: 138px;
    align-items: center;
    justify-content: space-between; }
  .footer__nav {
    display: flex;
    align-items: center; }
  .footer__logo {
    padding-right: 40px; }

.social__list {
  display: flex; }

.social__item:not(:last-child) {
  margin-right: 33px; }

/*==========  Desktop First Method  ==========*/
/* 
По умолчанию пишем стили для больших экранов. 
Desktop - настольный компьютер, с разрешение от 1200px и более. 
*/
/* Large Devices, Wide Screens */
/* Для экранов с шириной 1999px и менее. Ноутбуки (Laptop) */
@media (max-width: 1190px) {
  .header__middle {
    margin-left: 0; }
  .nav__item:not(:last-child) {
    margin-right: 23px; }
  .nav__link {
    font-size: 14px; }
  .journey {
    padding: 100px 0; }
  .advantages {
    padding: 100px 0; }
  .cruises {
    padding: 100px 0; }
    .cruises__list {
      margin-right: 0; }
    .cruises__item {
      width: 309px;
      margin: 0 15px 30px; }
      .cruises__item img {
        width: 100%; }
    .cruises .reservation {
      padding: 100px 0; }
    .cruises .contacts {
      padding: 100px 0; }
  .advantages__list {
    justify-content: center; }
  .advantages__item {
    flex-basis: 206px;
    padding: 25px; }
    .advantages__item:not(:last-child) {
      margin-right: 30px; }
  .reservation__inner {
    flex-direction: column; }
  .reservation__descr {
    text-align: center;
    max-width: 100%;
    margin-bottom: 60px; }
  .contacts__map {
    margin-right: 30px; }
  .footer .nav__link {
    font-size: 12px; } }

/* Medium Devices, Desktops */
/* Для экранов с шириной 991px и менее. Планшеты (Tablets) */
/* Small Devices, Tablets */
/* Для экранов с шириной 767px и менее. Планшеты (Tablets) и большие смартфоны. */
@media only screen and (max-width: 767px) {
  .journey {
    padding: 65px 0; }
    .journey__row {
      flex-direction: column; }
    .journey__text {
      margin-right: 0; }
  .advantages {
    padding: 65px 0; }
    .advantages__list {
      justify-content: center;
      flex-wrap: wrap; }
    .advantages__item {
      flex-basis: 100%;
      padding: 25px; }
      .advantages__item:not(:last-child) {
        margin-right: 0px;
        margin-bottom: 30px; }
  .cruises {
    padding: 65px 0; }
    .cruises__list {
      margin-right: 0; }
    .cruises__item {
      width: 309px;
      margin: 0 15px 30px; }
      .cruises__item img {
        width: 100%; }
  .reservation {
    padding: 65px 0; }
  .contacts {
    padding: 65px 0; }
    .contacts__inner {
      flex-direction: column; }
    .contacts__map {
      margin-right: 0; } }

/* Extra Small Devices, Phones */
/* Для экранов с шириной 480px и менее. Смартфоны */
@media only screen and (max-width: 480px) {
  .cruises {
    padding: 65px 0; }
    .cruises__list {
      margin-right: 0; }
    .cruises__item {
      width: 270px;
      margin: 0 15px 30px; }
      .cruises__item img {
        width: 100%; } }

